import React from 'react'


const WeatherForm = ({provinces}) => {

    const options = [];

    for (let i = 1; i <= provinces.length; i++) {
        options.push(
            <option value={provinces[i-1].CAPITAL_PROVINCIA}>{provinces[i-1].CAPITAL_PROVINCIA}</option>
        );
    }
    return (
        <form 
            onSubmit={e => {
                e.preventDefault();
                console.log('HOLAAA')
                }}
        >
            <div class="mb-3">
                <label htmlFor="provinceInput" class="form-label">Provincia</label>
                <input type="text" class="form-control" id="provinceInput" />

                <select class="form-select" aria-label="Default select example">
                    <option selected>Open this select menu</option>
                   {options}
                </select>

                <div className="d-grid gap-2 mx-auto col-6 p-3">
                    <button type="submit" className="btn btn-primary" data-bs-dismiss="modal">
                        Save
                    </button>
                </div>
            </div>
        </form>
    )
}

export default WeatherForm